@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800&display=swap');

* {
    box-sizing: border-box;
    color: #333;
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
}

body {
    overflow: hidden;
}

h1 {
    font-size: 42px;
    font-weight: 800;
    line-height: 51px;
}

h2 {
    font-size: 27px;
    font-weight: bold;
    line-height: 33px;
}

h3 {
    font-size: 20px;
    font-weight: bold;
    line-height: 24px;
}

h4 {
    font-size: 15px;
    font-weight: bold;
    line-height: 18px;
}

p {
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
}

@media screen and (max-width: 500px) {
    h1 {
        font-size: 27px;
        font-weight: 800;
        line-height: 33px;
    }

    h2 {
        font-size: 16px;
        font-weight: bold;
        line-height: 20px;
    }

    h3 {
        font-size: 15px;
        font-weight: bold;
        line-height: 18px;
    }

    h4 {
        font-size: 13px;
        font-weight: bold;
        line-height: 16px;
    }

    p {
        font-size: 14px;
        font-weight: 500;
        line-height: 17px;
    }
}
